import React, { Component, useState  } from 'react';

import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select'
import Checkbox from '@material-ui/core/Checkbox';
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

import { getObjectbyId } from '../utils';


function LumbersTable (props) {
  const { lumber_records } = props
  return (
    <table className='table table-sm table-responsive mt-2'>
      <thead>
        <th>
          
        </th>
        <th>
          -
        </th>
        <th>
          
        </th>
        <th>
            13
        </th>
        <th>
           
        </th>
      </thead>
      <tbody>
        {lumber_records.map(lumber_record =>
        <tr>
          <td>{lumber_record.name}</td>
          <td>{lumber_record.quantity} </td>
          <td>{lumber_record.volume_total} 3</td>
          <td>{lumber_record.selling_price} </td>
          <td>{lumber_record.selling_total_cash} </td>
        </tr>
          )}
      </tbody>
    </table>
  )
}

export function SaleSchema1ToCreate (props) {
  const { sale, saveData, back, sellers } = props
  const rowClass = 'mb-0 font-15 font-500 color-black'
  let seller = null
  if (sellers)
    seller = getObjectbyId(sellers, sale.seller)
  return (
    <div className='card card-style mt-2'>
      <div className='content'>
        <h4> </h4>
        {sale &&
          <div className=''>
            {/* <p className={rowClass}>: {sale.date}</p> */}
            <p className={rowClass}>: {sale.volume.toFixed(4)} 3</p>
            <p className={rowClass}>: {sale.sale_cash.toFixed(1)} </p>
            <p className={rowClass}>: {sale.loader ? '' : ""} </p>
            <p className={rowClass}> : {sale.bonus_kladman ? '' : ''}</p>
            <p className={rowClass}>: {seller ? seller.nickname : ''}</p>
            <p className={rowClass}>: {sale.deliveryFee ? sale.deliveryFee + '' : ''}</p>
            <p className={rowClass}>. : {sale.add_expenses ? sale.add_expenses + '' : ''}</p>
            
            <p className={rowClass}>: {sale.note}</p>

            <LumbersTable lumber_records={sale.raw_records}/>
          </div>
        }
        <div className='d-flex justify-content-around'>
          <button onClick={back}
            className='btn btn-s mr-2 text-uppercase font-900 bg-dark color-white rounded-sm shadow-l'>
            
          </button>
          <button onClick={saveData}
            className='btn btn-s text-uppercase font-900 bg-highlight rounded-sm shadow-l'>
                 
          </button>
        </div>
      </div>
    </div>
  )
}

export function CreatedSale (props) {
  const { sale, message } = props
  const rowClass = 'mb-0 font-15 font-500 color-black'
  return (
    <div className='card card-style mt-2'>
      <div className='content'>
        <h4> </h4>
        {sale &&
          <div className=''>
            <p className={rowClass}>: {sale.date}</p>
            <p className={rowClass}>: {sale.volume.toFixed(4)} 3</p>
            <p className={rowClass}> : {sale.selling_total_cash.toFixed(1)}  </p>
            <p className={rowClass}> : {sale.seller_fee} </p>
            {/* <p className={rowClass}>  (): {sale.rama_total_cash.toFixed(1)}  </p> */}
            <p className={rowClass}> : {sale.loader_fee} </p>
            <p className={rowClass}> : {sale.kladman_fee} </p>
            <p className={rowClass}> : {sale.delivery_fee} </p>
            <p className={rowClass}>. : {sale.add_expenses} </p>
            {/* <p className={rowClass}>  (): {sale.net_rama_cash.toFixed(1)}  </p> */}
            
            <p className={rowClass}>: {sale.note}</p>

            <LumbersTable lumber_records={sale.lumber_records}/>
          </div>
        }
        {message &&
          <h2 className='color-green1-light text-center'> !</h2> 
        }
      </div>
    </div>
  )
}

export function AddNote (props) {
  const { note, setNote } = props
  return (
    <TextField 
      className=''
      type='textarea' value={note} name='note' onChange={setNote} multiline={true} rowsMax={5}
      id="standard" variant="outlined" className='d-block' label='' placeholder='   '
    />
  )
}

export function LumberToSale (props) {
  const { lumber, setLumberID, calcRowQnty, calcRowCash, setRowCash, pineBrus, larchBrus, pineDoska,
     larchDoska, calcRowRamaCash, calcRowVolume, calcRowCash1Pc } = props
  console.log(lumber)
  return (
    <div className='mt-2 mb-3 px-2 py-3 bg-white-dark rounded-m border'>
      <InputLabel htmlFor="grouped-native-select" className='font-19 font-600 color-black'>
         {lumber.id + 1}
      </InputLabel>
      <Select 
        native 
        defaultValue="" 
        id="grouped-native-select" 
        className='color-dark1-light'
        fullWidth
        value={lumber.lumber} onChange={(e) =>setLumberID(e, lumber.id)}
        name='lumber'
        data-id={lumber.id}
        defaultValue={null}
        >
        <option aria-label="None" />
        <optgroup label=" ">
          {pineBrus.map(pb =>
            <option value={pb.id} >{`${pb.name} ${pb.wood_species}`} </option>
            )}
        </optgroup>
        <optgroup label=" ">
          {larchBrus.map(lb =>
            <option value={lb.id} >{`${lb.name} ${lb.wood_species}`} </option>
            )}
        </optgroup>
        <optgroup label=" ">
          {pineDoska.map(pd =>
            <option value={pd.id} >{`${pd.name} ${pd.wood_species}`} </option>
            )}
        </optgroup>

      </Select>
      {lumber.lumber > 0 && 
        <p className='my-0'>
          <span className='mr-2'>1 = {lumber.volume} 3</span>
          <span className='mr-2'>13 = {lumber.qnty_in_cube} </span>
          <span className='mr-2'>23 = {lumber.qnty_in_cube * 2} </span>
          <span className='mr-2'>33 = {(lumber.qnty_in_cube * 3).toFixed(3)} </span>
        </p>
      }
      {lumber.lumber > 0 && 
        <div className=''>
          <div className='my-2'>
            <h5>  :</h5>
            <div className='d-flex justify-content-start '>
              <TextField 
                className='pr-2'
                id="outlined-margin-dense" 
                variant="outlined" 
                label=''
                onChange={(e) => calcRowQnty(e, lumber.id)} type='number'
                value={lumber.quantity > 0 && lumber.quantity}/>

              <TextField 
                className=''
                id="outlined-margin-dense" 
                variant="outlined" 
                label=''
                onChange={(e) => calcRowVolume(e, lumber.id)}
                type='number'
                value={lumber.selling_volume_total > 0 && lumber.selling_volume_total}/>
            </div>

            <div className='d-flex justify-content-start mt-2'>
              <TextField 
                className='pr-2'
                id="outlined-margin-dense"
                variant="outlined" 
                label='  13'
                onChange={(e) => calcRowCash(e, lumber.id)} type='number'
                value={lumber.selling_price > 0 && lumber.selling_price}/>

              <TextField 
                className=''
                id="outlined-margin-dense"
                variant="outlined" 
                label=''
                onChange={(e) => setRowCash(e, lumber.id)} type='number'
                value={lumber.selling_total_cash > 0 && lumber.selling_total_cash}/>
            </div>
          </div>
          <div className=''>
            <h5> :</h5>
            <div className='d-flex justify-content-start mt-1'>
              <p className='font-16 color-dark mb-0 mr-4'>: 
                <span className='font-500'> {lumber.volume_total + ' 3'}</span>
              </p>
              <p className='font-16 color-dark mb-0'> : 
                <span className='font-500'> {lumber.rama_total_cash > 0 && lumber.rama_total_cash + ' '}</span>
              </p>
            </div>
              <TextField 
                className=''
                variant="outlined" 
                label='  13'
                onChange={(e) => calcRowRamaCash(e, lumber.id)} type='number'
                value={lumber.rama_price > 0 && lumber.rama_price}
                defaultValue={lumber.market_cost}
                />
          </div>
        </div>}
    </div>
  )
}

export function DeliveryFeeComp (props) {
  const { setDeliveryFee, deliveryFee } = props

  return (
    <div className='my-1'>
      <TextField 
        className=''
        id="outlined-margin-dense"
        variant="outlined" 
        label=''
        onChange={setDeliveryFee} type='number'
        value={deliveryFee > 0 && deliveryFee}/>
    </div>
  )
}


export function Schema1CheckList (props) {
  const { loader, checkLoader, seller, bonusKladman, sellers } = props

  return (
    <div className='d-flex justify-content-between mb-1 '>
      <FormControlLabel
        control={
          <Checkbox
            checked={loader}
            onChange={checkLoader}
            color="blue2"
            name='loader'
          />
        }
        label=""
      />
      <FormControlLabel
        control={
          <Checkbox
            checked={bonusKladman}
            onChange={checkLoader}
            color="blue2"
            name='bonusKladman'
          />
        }
        label=" "
      />
      <FormControl  className='pb-4'>
        <InputLabel ></InputLabel>
        <Select 
          native 
          defaultValue="" 
          className='color-dark1-light'
          value={seller} onChange={checkLoader}
          name='seller'
          defaultValue={null}
          >
          <option aria-label="None" />
          {sellers && sellers.length > 0 && sellers.map(s => 
            <option value={s.id} >{s.nickname} </option>
          )}
        </Select>
      </FormControl>
    </div>  
    )
}
